nBO","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');\r\n\r\n$text-hover: #d3ad7f;\r\n$text-black: #424242;\r\n$text-white: #efefef;\r\n$text-detail: #394867;\r\n$bg-1: #333333;\r\n$brd: 0.3px solid black;\r\n$top-padding: 70px;\r\n$border-col: #e5ebf8;\r\n\r\n* {\r\n  font-family: 'Poppins', Tahoma ,sans-serif;\r\n  margin: 0; padding: 0;\r\n  box-sizing: border-box;\r\n  outline: none;\r\n  text-decoration: none;\r\n  text-transform: capitalize;\r\n  color: $text-black;\r\n}\r\nhtml {\r\n  scroll-behavior: smooth;\r\n  scroll-padding-top: $top-padding;\r\n  overflow-x: hidden;\r\n}\r\n\r\nimg {\r\n  display: flex;\r\n  width: 100%;\r\n  user-select: none;\r\n}\r\na, .menu-icon, button, input {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  cursor: pointer;\r\n  min-height: 45px;\r\n  min-width: 45px;\r\n}\r\n/* menhilangkan efek biru pada saat klik link di mobile */\r\n*, *:before, *:after {\r\n  box-sizing: border-box;\r\n  -moz-box-sizing: border-box;\r\n  -webkit-box-sizing: border-box;\r\n  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);\r\n  -webkit-tap-highlight-color: transparent;\r\n}\r\n/* membuat image, dan a tidak dapat di drag */\r\nimg, a {\r\n    // user-drag: none;\r\n    -webkit-user-drag: none;\r\n    user-select: none;\r\n    -moz-user-select: none;\r\n    -webkit-user-select: none;\r\n    -ms-user-select: none;\r\n}\r\n\r\n.title {\r\n  text-align: center;\r\n  padding: 10px 0;\r\n  text-align: center;\r\n}\r\n\r\n/* body */\r\nbody {\r\n  padding-top: $top-padding;\r\n}\r\n\r\n/* header */\r\nheader {\r\n  height: $top-padding;\r\n  padding: 0 30px;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  position: fixed;\r\n  top: 0;\r\n  width: 100%;\r\n  background: $bg-1;\r\n  color: $text-white;\r\n  z-index: 10;\r\n  .header-logo, \r\n  .menu-icon, \r\n  .menu-container {\r\n    z-index: 10;\r\n  }\r\n  .header-logo {\r\n    height: 100%;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    img {\r\n      padding: 10px;\r\n      object-fit: cover;\r\n      height: $top-padding;\r\n      width: auto;\r\n      background: $bg-1;\r\n    }\r\n    h1 {\r\n      height: 100%;\r\n      display: flex;\r\n      justify-content: center;\r\n      align-items: center;\r\n      color: $text-white;\r\n      background: $bg-1;\r\n      font-size: 1.3rem;\r\n      text-wrap: nowrap;\r\n    }\r\n  }\r\n  .menu-icon {\r\n    padding-right: 30px;\r\n    display: none;\r\n    justify-content: center;\r\n    align-items: center;\r\n    height: $top-padding;\r\n    font-size: 1.8rem;\r\n    color: $text-white;\r\n  }\r\n  .nav ul {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    column-gap: 40px;\r\n    list-style: none;\r\n  }\r\n  .nav li a {\r\n    position: relative;\r\n    color: $text-white;\r\n  }\r\n}\r\n\r\n@media screen and (max-width: 700px) {\r\n  header{\r\n    padding: 0;\r\n    .header-logo {\r\n      h1 {\r\n        font-size: 1rem;\r\n      }\r\n    }\r\n    .menu-icon {\r\n      display: flex !important;\r\n      background: $bg-1;\r\n      border: none;\r\n    }\r\n    .menu-container {\r\n      display: flex;\r\n      justify-content: flex-end;\r\n      flex-grow: 1;\r\n      width: 100%;\r\n      background: $bg-1;\r\n    }\r\n    .nav {\r\n      top: -250px; left: 0;\r\n      width: 100%;\r\n      position: absolute;\r\n      background: $bg-1;\r\n      transition: all 0.5s ease-in-out;\r\n      ul {\r\n        padding-bottom: 10px;\r\n        flex-direction: column;\r\n        row-gap: 10px;\r\n      }\r\n    }\r\n  }\r\n  .open {\r\n    top: $top-padding !important;\r\n  }\r\n}\r\n\r\n/* main */\r\nsection {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: baseline;\r\n  align-items: center;\r\n  min-height: 100vh;\r\n  width: 100%;\r\n}\r\nmain {\r\n  padding: 0 10px 10px 10px;\r\n  width: 100%;\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.hero {\r\n  position: relative;\r\n}\r\nrestaurant-search form {\r\n  padding: 10px 0;\r\n  display: grid;\r\n  grid-template-columns: 40% auto;\r\n  justify-content: center;\r\n  input {\r\n    min-width: 100px !important;\r\n    padding: 10px;\r\n    display: flex;\r\n    background: inherit;\r\n    border: 1px solid $text-black;\r\n    border-radius: 8px;\r\n  }\r\n  button {\r\n    margin-left: -10px;\r\n    padding: 10px;\r\n    display: flex;\r\n    background: $text-detail;\r\n    color: $text-white;\r\n    border-radius: 8px;\r\n    &:focus {\r\n      color: #d3ad7f;\r\n    }\r\n  }\r\n}\r\n\r\n/* restaurant-list */\r\nrestaurant-list {\r\n  display: grid;\r\n  flex-direction: column;\r\n  grid-template-columns: repeat(3, 1fr);\r\n  gap: 10px;\r\n}\r\n\r\n@media screen and (max-width: 700px) {\r\n  restaurant-search form {\r\n    grid-template-columns: 90% auto !important;\r\n  }\r\n  restaurant-list {\r\n      grid-template-columns: repeat(1, 1fr) !important;\r\n  }\r\n}\r\n@media screen and (max-width: 900px) {\r\n  restaurant-search form {\r\n    grid-template-columns: 60% auto;\r\n  }\r\n  restaurant-list {\r\n      grid-template-columns: repeat(2, 1fr);\r\n  }\r\n}\r\n\r\nrestaurant-item {\r\n  display: grid;\r\n  grid-template-rows: 200px auto;\r\n  align-content: flex-start;\r\n  flex-direction: column;\r\n  overflow: hidden;\r\n  background: #ffffff;\r\n  overflow: hidden;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  border-radius: 5px;\r\n  transition: all 0.3s ease-in-out;\r\n  .card-image img, .card-content {\r\n    width: 100% !important;\r\n  }\r\n  &:hover {\r\n    transform: translate(0, -4px);\r\n  }\r\n  :focus {\r\n    background: $text-white !important;\r\n  }\r\n}\r\n\r\n.card-image {\r\n  position: relative;\r\n  display: flex;\r\n  overflow: hidden;\r\n  transition: all none;\r\n  transition: all 0.3s ease-in-out;\r\n  img {\r\n    object-fit: cover;\r\n    object-position: center;\r\n    -o-object-fit: cover;\r\n    transition: all 0.3s ease-in-out;\r\n  }\r\n  h3 {\r\n    position: absolute;\r\n    // top: 0; left: 0;\r\n    color: $text-white;\r\n    padding: 5px 15px;\r\n    background-color: rgba(0, 0, 0, 0.5);\r\n    font-weight: 500;\r\n    border-radius: 5px 0 5px 0;\r\n  }\r\n  i {\r\n    position: absolute;\r\n    top: 0; right: 0;\r\n    padding: 8px;\r\n    color: $text-white;\r\n    font-size: 22px;\r\n  }\r\n  &:hover img {\r\n    transform: scale(1.05);\r\n  }\r\n}\r\n\r\n.card-content {\r\n  // max-height: 200px;\r\n  padding: 10px 20px;\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 2px;\r\n  position: relative;\r\n  h3 {\r\n    color: #999;\r\n    font-size: 15px;\r\n    position: absolute;\r\n    right: 0;\r\n    padding: 0 20px;\r\n  }\r\n  h2 {\r\n    display: flex;\r\n    padding-right: 35px;\r\n    font-size: 1.3rem;\r\n    letter-spacing: 0;\r\n    white-space: wrap;\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    a:hover, a:focus {\r\n      color: $text-hover !important;\r\n      background: none !important;\r\n    }\r\n  }\r\n  p {\r\n    font-size: 0.8rem;\r\n    white-space: wrap;\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    display: -webkit-box;\r\n    -webkit-line-clamp: 10;\r\n    -webkit-box-orient: vertical;\r\n  }\r\n}\r\n\r\n/* detail */\r\nrestaurant-detail {\r\n  $p-detail: 20px;\r\n  width: 100% !important;\r\n  display: flex;\r\n  flex-direction: column;\r\n  padding: 10px 50px !important;\r\n  gap: 20px 20px;\r\n  .title {\r\n    border-top: 1px solid $text-black;\r\n  }\r\n  .detail-title {\r\n    display: flex;\r\n    align-items: center;\r\n    flex-wrap: wrap;\r\n    grid-column: 2 span;\r\n    border-bottom: 3px solid $text-black;\r\n    h1 {\r\n      display: flex;\r\n      align-items: center;\r\n      padding-right: 10px;\r\n      font-size: 1.8em;\r\n      a {\r\n        font-size: 1em;\r\n        &:hover {\r\n          color: $text-hover;\r\n        }\r\n      }\r\n    }\r\n  }\r\n  .detail-content {\r\n    img {\r\n      width: 60%;\r\n      float: left;\r\n      margin-right: 15px;\r\n      border-radius: 12px;\r\n      object-fit: cover;\r\n    }\r\n    h1 {\r\n      display: grid;\r\n      grid-template-columns: 85px auto;\r\n      font-size: 1em;\r\n      opacity: 0.9;\r\n      &:last-child {\r\n        padding-bottom: 10px;\r\n        border-bottom: 1px solid $text-black;\r\n      }\r\n      span {\r\n        color: $text-detail;\r\n        &::before {\r\n          content: \": \";\r\n          color: $text-black;\r\n        }\r\n      }\r\n    }\r\n    p {\r\n      padding-top: 10px;\r\n      text-indent: 30px;\r\n      text-align: justify;\r\n      line-height: 1.3;\r\n    }\r\n  }\r\n\r\n  .detail-menu {\r\n    display: grid;\r\n    grid-template-columns: repeat(2, 1fr);\r\n    gap: 20px;\r\n    div {\r\n      padding: 5px 10px;\r\n      padding-bottom: 15px;\r\n      border: 0.25px solid $border-col;\r\n      border-radius: 8px;\r\n      h1 {\r\n        text-align: center;\r\n        font-size: 1.3em;\r\n      }\r\n    }\r\n    ul {\r\n      display: grid;\r\n      flex-direction: row;\r\n      gap: 5px;\r\n      grid-template-columns: repeat(4, 1fr);\r\n      list-style-type: none !important;\r\n      li {\r\n        padding: 2px;\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n        font-size: 0.8rem;\r\n        text-align: center;\r\n        border-radius: 8px;\r\n        background: #eff2f8;\r\n        &:hover {\r\n          background: $text-white;\r\n        }\r\n      }\r\n    }\r\n  }\r\n  .detail-review {\r\n    svg {\r\n      display: flex;\r\n      height: 30px;\r\n      font-size: 0.5rem;\r\n    }\r\n    ul {\r\n      list-style-type: none;\r\n      display: grid;\r\n      grid-template-columns: repeat(3, 1fr);\r\n      gap: 10px;\r\n    }\r\n    li {\r\n      border: 1px solid $text-black;\r\n      border-radius: 8px;\r\n      padding: 10px;\r\n    }\r\n    .review-title {\r\n      font-size: 1rem;\r\n      display: flex;\r\n      border-bottom: 1px solid $text-black;\r\n      h1 {\r\n        padding-left: 5px;\r\n        display: flex;\r\n        justify-content: space-between;\r\n        align-items: center;\r\n        flex-grow: 1;\r\n        font-size: 1.3rem;\r\n        span {\r\n          font-size: 0.9rem;\r\n        }\r\n      }\r\n    }\r\n    p {\r\n      padding: 5px 10px;\r\n    }\r\n  }\r\n  .detail-form {\r\n    display: flex;\r\n    justify-content: center;\r\n    .form-review {\r\n      width: 50%;\r\n      display: flex;\r\n      flex-direction: column;\r\n      padding: 35px;\r\n      gap: 10px;\r\n      border-radius: 6px;\r\n      border: 1px solid $text-black;\r\n      label {\r\n        display: flex;\r\n        font-size: 1rem;\r\n      }\r\n      input {\r\n        height: 35px;\r\n        display: flex;\r\n        font-size: 1rem;\r\n        padding: 10px;\r\n        border-radius: inherit;\r\n        border: inherit;\r\n      }\r\n      textarea {\r\n        display: flex;\r\n        padding: 10px;\r\n        border-radius: inherit;\r\n        border: inherit;\r\n      }\r\n      button {\r\n        background: #394867;\r\n        color: $text-white;\r\n        border-radius: inherit;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.like {\r\n  height: 55px; width: 55px;\r\n  position: fixed;\r\n  right: 16px; bottom: 16px;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  font-size: 18px;\r\n  border-radius: 50%;\r\n  border: none;\r\n  // background: $text-black;\r\n  color: $text-white;\r\n  cursor: pointer;\r\n}\r\n\r\n@media screen and (max-width: 700px) {\r\n  restaurant-detail {\r\n      font-size: 90%;\r\n      padding: 10px 10px !important;\r\n    img {\r\n      min-width: 100% !important;\r\n      float: none !important;\r\n      padding-bottom: 10px;\r\n    }\r\n    .detail-menu {\r\n      grid-template-columns: repeat(1, 1fr);\r\n    }\r\n    .detail-review {\r\n      ul {\r\n        grid-template-columns: repeat(1, 1fr) !important;\r\n      }\r\n    }\r\n    .detail-form {\r\n      .form-review {\r\n        width: 100% !important;\r\n      }\r\n    }\r\n  }\r\n}\r\n@media screen and (max-width: 900px) {\r\n  restaurant-detail {\r\n    .detail-title {\r\n      h1 {\r\n        font-size: 1.3rem;\r\n      }\r\n    }\r\n    img {\r\n      width: 50% !important;\r\n    }\r\n    .detail-menu {\r\n      ul {\r\n        grid-template-columns: repeat(2, 1fr) !important;\r\n      }\r\n    }\r\n    .detail-review {\r\n      ul {\r\n        grid-template-columns: repeat(2, 1fr);\r\n      }\r\n    }\r\n    .detail-form {\r\n      .form-review {\r\n        width: 70%;\r\n      }\r\n    }\r\n  }\r\n}\r\n@media screen and (max-width: 1100px) {\r\n  restaurant-detail {\r\n    .detail-menu {\r\n      ul {\r\n        grid-template-columns: repeat(3, 1fr);\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n\r\n// responsive\r\n@media screen and (min-width: 1200px) {\r\n  body {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n  }\r\n  main {\r\n    width: 1000px;\r\n  }\r\n}\r\n\r\n// focus\r\nheader .nav a:focus,\r\nmain .title:focus {\r\n  color: $text-hover;\r\n}\r\n\r\n// skip link\r\n.skip-link {\r\n  position: fixed;\r\n  top: -100px;\r\n  left: 0;\r\n  background-color: $bg-1 !important;\r\n  color: $text-white !important;\r\n  padding: 8px;\r\n  z-index: 100;\r\n  &:focus {\r\n     top: $top-padding;\r\n     color: $text-hover !important;\r\n  }\r\n}\r\n\r\n// page-none\r\n.page-none-container {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  grid-row: span 3;\r\n  grid-column: span 3;\r\n  height: 50vh;\r\n  // border: $brd;\r\n}\r\n\r\n#page-none {\r\n  height: 50px;\r\n  width: 100%;\r\n  font-size: 1rem;\r\n  text-align: center;\r\n  text-wrap: wrap;\r\n  span {\r\n    margin: 0 5px;\r\n    color: $text-black;\r\n    cursor: pointer;\r\n    &:hover {\r\n      color: $text-hover;\r\n    }\r\n  }\r\n}"],"sourceRoot":""}]);
